"use client"

import { useState } from "react"
import {
  Dialog,
  DialogContent,
  DialogDescription,
  DialogHeader,
  DialogTitle,
  DialogFooter,
} from "@/components/ui/dialog"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"

interface CreateDriverModalProps {
  open: boolean
  onOpenChange: (open: boolean) => void
  onSubmit: (data: any) => void
}

export function CreateDriverModal({ open, onOpenChange, onSubmit }: CreateDriverModalProps) {
  const [formData, setFormData] = useState({
    nom: "",
    email: "",
    password: "",
    telephone: "",
    numeroPermis: "",
    busAssigne: "",
  })
  const [errors, setErrors] = useState<Record<string, string>>({})

  const validateForm = () => {
    const newErrors: Record<string, string> = {}

    if (!formData.nom || formData.nom.length < 2 || formData.nom.length > 100) {
      newErrors.nom = "Name must be between 2-100 characters"
    }
    if (!formData.email || !/^[^\s@]+@[^\s@]+\.[^\s@]+$/.test(formData.email)) {
      newErrors.email = "Invalid email address"
    }
    if (!formData.password || formData.password.length < 6) {
      newErrors.password = "Password must be at least 6 characters"
    }
    if (!formData.numeroPermis) {
      newErrors.numeroPermis = "License number is required"
    }
    if (!formData.busAssigne) {
      newErrors.busAssigne = "Assigned bus is required"
    }

    setErrors(newErrors)
    return Object.keys(newErrors).length === 0
  }

  const handleSubmit = () => {
    if (validateForm()) {
      onSubmit({
        ...formData,
        role: "CONDUCTEUR",
      })
      setFormData({
        nom: "",
        email: "",
        password: "",
        telephone: "",
        numeroPermis: "",
        busAssigne: "",
      })
    }
  }

  return (
    <Dialog open={open} onOpenChange={onOpenChange}>
      <DialogContent className="sm:max-w-[425px]">
        <DialogHeader>
          <DialogTitle>Créer Conducteur</DialogTitle>
          <DialogDescription>Create a new driver account</DialogDescription>
        </DialogHeader>

        <div className="space-y-4 py-4">
          <div>
            <Label htmlFor="nom">Nom *</Label>
            <Input
              id="nom"
              value={formData.nom}
              onChange={(e) => setFormData({ ...formData, nom: e.target.value })}
              className={errors.nom ? "border-red-500" : ""}
            />
            {errors.nom && <p className="text-sm text-red-500 mt-1">{errors.nom}</p>}
          </div>

          <div>
            <Label htmlFor="email">Email *</Label>
            <Input
              id="email"
              type="email"
              value={formData.email}
              onChange={(e) => setFormData({ ...formData, email: e.target.value })}
              className={errors.email ? "border-red-500" : ""}
            />
            {errors.email && <p className="text-sm text-red-500 mt-1">{errors.email}</p>}
          </div>

          <div>
            <Label htmlFor="password">Password *</Label>
            <Input
              id="password"
              type="password"
              value={formData.password}
              onChange={(e) => setFormData({ ...formData, password: e.target.value })}
              className={errors.password ? "border-red-500" : ""}
            />
            {errors.password && <p className="text-sm text-red-500 mt-1">{errors.password}</p>}
          </div>

          <div>
            <Label htmlFor="telephone">Téléphone</Label>
            <Input
              id="telephone"
              value={formData.telephone}
              onChange={(e) => setFormData({ ...formData, telephone: e.target.value })}
            />
          </div>

          <div>
            <Label htmlFor="numeroPermis">Numéro de Permis *</Label>
            <Input
              id="numeroPermis"
              value={formData.numeroPermis}
              onChange={(e) => setFormData({ ...formData, numeroPermis: e.target.value })}
              className={errors.numeroPermis ? "border-red-500" : ""}
            />
            {errors.numeroPermis && <p className="text-sm text-red-500 mt-1">{errors.numeroPermis}</p>}
          </div>

          <div>
            <Label htmlFor="busAssigne">Bus Assigné *</Label>
            <Input
              id="busAssigne"
              value={formData.busAssigne}
              onChange={(e) => setFormData({ ...formData, busAssigne: e.target.value })}
              className={errors.busAssigne ? "border-red-500" : ""}
            />
            {errors.busAssigne && <p className="text-sm text-red-500 mt-1">{errors.busAssigne}</p>}
          </div>
        </div>

        <DialogFooter>
          <Button type="button" variant="outline" onClick={() => onOpenChange(false)}>
            Annuler
          </Button>
          <Button type="button" className="bg-[#1e40af] text-white hover:bg-[#1e3a8a]" onClick={handleSubmit}>
            Créer Conducteur
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  )
}
