'use client'

import { Bus, MapPin, User } from 'lucide-react'

interface SidebarProps {
  currentPage: string
  onNavigate: (page: string) => void
}

export default function Sidebar({ currentPage, onNavigate }: SidebarProps) {
  const menuItems = [
    { id: 'lines', label: 'Gestion des Lignes', icon: Bus },
    { id: 'stops', label: 'Gestion des Arrêts', icon: MapPin },
    { id: 'users', label: 'Gestion Users', icon: User },
  ]

  return (
    <aside className="w-64 bg-gray-50 text-gray-800 border-r border-gray-200 p-6 flex flex-col">
      {/* Logo / Header */}
      <div className="mb-8">
        <h1 className="text-2xl font-bold text-[#1e40af]">CityTransit</h1>
        
      </div>

      {/* Navigation */}
      <nav className="flex-1 space-y-2">
        {menuItems.map((item) => {
          const Icon = item.icon
          const isActive = currentPage === item.id

          return (
            <button
              key={item.id}
              onClick={() => onNavigate(item.id)}
              className={`w-full flex items-center gap-3 px-4 py-3 rounded-lg transition-all duration-150 ${
                isActive
                  ? 'bg-[#1e40af] text-white shadow-md'
                  : 'text-gray-700 hover:bg-gray-200'
              }`}
            >
              <Icon size={20} />
              <span className="font-medium">{item.label}</span>
            </button>
          )
        })}
      </nav>

      {/* Footer */}
      <div className="pt-4 border-t border-gray-200">
        <p className="text-xs text-gray-500">© 2025 TransitHub</p>
      </div>
    </aside>
  )
}
